'use client';

import { useEffect, useState } from 'react';
import Image from 'next/image';
import { db } from '@/src/firebaseConfig';
import { collection, getDocs } from 'firebase/firestore';

interface EbikeModel {
  id: string;
  name: string;
  price: string;
  image: string;
}

function EbikeModelList({ ebikemodels }: { ebikemodels: EbikeModel[] }) {
  return (
    <section className="font-sans py-12 px-3 bg-gray-100 text-center">
      <div className="grid md:grid-cols-4 gap-3 justify-items-center">
        {ebikemodels.map((ebikemodel) => (
          <div key={ebikemodel.id} className="bg-white text-gray-800 p-6 rounded-lg shadow-md">
            <Image src={ebikemodel.image} alt={ebikemodel.name} width={300} height={200} className="rounded-lg" />
            <h3 className="text-xl font-semibold mt-4">{ebikemodel.name}</h3>
            <p className="text-gray-600">{Number(ebikemodel.price).toLocaleString()} VND/day</p>
            <button className="mt-4 px-6 py-2 bg-[#00d289] font-semibold text-white rounded-sm">
              Rent Now
            </button>
          </div>
        ))}
      </div>
    </section>
  );
}

export default function EbikeModelsPage() {
  const [ebikemodels, setEbikemodels] = useState<EbikeModel[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchModels = async () => {
      try {
        const snapshot = await getDocs(collection(db, 'ebikemodels'));
        const data = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() })) as EbikeModel[];
        setEbikemodels(data);
      } catch (error) {
        console.error('Error fetching ebike models:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchModels();
  }, []);

  return (
    <div>
      <h1 className="font-sans text-gray-900 text-2xl bg-gray-100 font-bold text-center px-3">
        Electric Motorbike Rental List
      </h1>
      {loading ? (
        <p className="text-center mt-6">‚è≥ Loading data...</p>
      ) : (
        <EbikeModelList ebikemodels={ebikemodels} />
      )}
    </div>
  );
}
